.input__wrapper {
  display: block;
  position: relative;
  margin: 25px 0;
}

.input__contact {
  display: block;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  background-color: #dedede;
  border-radius: 5px;
  border: none;
  width: 250px;
  height: 30px;
  transition: 0.2s ease-in;
  padding: 0 10px;
}

.input__contact:focus {
  outline: none;
  border-bottom: 3px solid #e67918;
}

.input__label {
  font-size: 14px;
  color: #fff;
  position: absolute;
  top: -20px;
  left: 0;
  transition: 0.2s ease-in;
}

.input__contact:-ms-input-placeholder + .input__label {
  opacity: 0;
  visibility: hidden;
  transform: translateY(20px);
}

.input__contact:placeholder-shown + .input__label {
  opacity: 0;
  visibility: hidden;
  transform: translateY(20px);
}

.textarea__contact {
  display: block;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  background-color: #dedede;
  border-radius: 5px;
  border: none;
  width: 250px;
  height: 150px;
  transition: 0.2s ease-in;
  padding: 10px;
}

