  color: #fff;
  width: 70%;
}

.contact__name {
  font-family: "Anton", sans-serif;
  font-weight: 500;
  font-size: 24px;
  text-transform: uppercase;
  padding: 10px 0;
}

.contact__surname {
  color: #e67918;
}

.contact__number {
  font-family: "Open Sans", sans-serif;
  font-weight: 700;
  padding-bottom: 10px;
  color: #fff;
  text-decoration: none;
}

.contact__email {
  font-family: "Open Sans", sans-serif;
  font-weight: 700;
}

.contact__adres {
  font-family: "Open Sans", sans-serif;
  font-weight: 700;
}

.contact__name-company,
.contact__street {
  padding-bottom: 10px;
}

@media (min-width: 640px) {
  .input__contact {
    width: 350px;
  }
  .textarea__contact {
    width: 350px;
  }
  .button__contact {
    width: 200px;
    margin-left: 42%;
  }
}

@media (min-width: 992px) {
  .form--container {
    width: 50%;
  }
  .contact--container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 50%;
    margin-top: 20px;
  }
}

@media (min-width: 1400px) {
  .contact__title {
    font-size: 40px;
  }

  .input__contact {
    width: 400px;
  }
  .textarea__contact {
    width: 400px;
    height: 300px;
  }
  .button__contact {
    width: 230px;
    margin-left: 42%;
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 16px;
    height: 35px;
  }
}

@media (min-width: 1600px) {
  .input__contact {
    width: 500px;
  }
  .textarea__contact {
    width: 500px;
  }
  .button__contact {
    width: 250px;
    margin-left: 50%;
  }
  .form--container {
    width: 40%;
  }
  .form__desc {
    padding: 20px 0px;
  }
  .contact--container {
    width: 40%;
  }
}

.footer {
  display: block;
  background-color: #0a130e;
  color: #777;
  text-align: right;
  font-size: 14px;
  padding: 30px 20px 10px 10px;
  font-family: "Open Sans", sans-serif;
  font-weight: 300;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  overflow-x: hidden;
}

::-webkit-scrollbar {
  width: 15px;
}
::-webkit-scrollbar-thumb {
  background: #e67918;
  border-radius: 20px;
}

::-webkit-scrollbar-thumb:hover {
  background: #b15c12;
}

